.postSummary {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  font-size: 1em;
  line-height: 1.5;
  margin: 0;
  padding: 10px 20px;
  position: relative;
  text-align: left;
  width: 100%;
}

.postSummary:focus,
.postSummary:hover {
  outline: none;
  background-color: rgba(255, 255, 255, 0.6);
}

.postSummary__label {
  -ms-flex-positive: 0;
      flex-grow: 0;
}

.postSummary__headline {
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0 0 0 15px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}



